@import './../../common/styles/vars';

form {
  display: flex;
  flex-direction: column;
  border-radius: 5px;
  padding: 20px;
  background: #333;
  color: antiquewhite;
  max-width: 300px;
  width: 100%;
  gap: 30px;

  @media screen and (max-width: $md) {
    max-width: 100%;
  }
}

label {
  display: flex;
  flex-direction: column;
  gap: 10px;
}

input,
button {
  padding: 5px;
  border-radius: 5px;
}
