@import './common/styles/vars';

body > div:nth-child(2) {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

header,
footer {
  background: $dark-color;
  color: $light-color;
}

header > div {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 10px;
  flex-wrap: wrap;
}

footer {
  margin-top: auto;
}

header > div,
main,
footer > p {
  max-width: 1200px;
  width: 100%;
  min-width: 300px;
  margin: 0 auto;
  padding: 20px;
}

footer > p {
  text-align: center;
}

main {
  margin: 0 auto;
  padding: 20px;
  display: flex;
  align-items: flex-start;
  gap: 20px;

  @media screen and (max-width: $md) {
    flex-direction: column;
  }
}
